* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Space Mono", monospace;
  font-family: "Spartan", sans-serif;
}

.card-wrapper {
  background-color: #ffffff;
  padding: 30px 20px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 13px rgba(0, 0, 0, 0.2);
  position: relative;
}

.lighter-shadow {
  box-shadow: 0px 10px 10px -10px rgba(72, 84, 159, 0.100397);
  margin-bottom: 16px;
}

button {
  width: 100%;
  padding: 12px 12px;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  justify-content: center;
}

.not-active {
  background-color: #ffffff;
  color: black;
  border: 1px solid #e9e9e9;
  transition-duration: 0.4s;
}

.active {
  background: rgb(101, 89, 169);
  background: linear-gradient(
    113deg,
    rgba(101, 89, 169, 0.8782201405152225) 0%,
    rgba(146, 119, 255, 0.9203747072599532) 48%
  );
  color: #ffffff;
  border: 1px solid #e9e9e9;
  transition-duration: 0.4s;
}

.active:hover {
  background-color: #7a5bf8;
}

.smaller-btn {
  width: 100px;
  border-radius: 100px;
  align-self: center;
  padding: 10px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.invoice-footer-btn {
  font-size: 12px;
  font-weight: bold;
  border-radius: 50px;
  width: 340px;
  padding: 0 15px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 3px;
}

.draft {
  background-color: #373c53;
  color: #ffffff;
  border: none;
}

.draft:hover {
  background-color: #0d0e17;
  color: #61667f;
}

.discard {
  width: 80px;
  background-color: #f9fafe;
  color: #7f88c3;
  border: none;
}

.discard:hover {
  background-color: #dfe3fa;
}

.delete {
  height: 48px;
  background-color: #ec5757;
}

.delete:hover {
  background-color: #fb9797;
}

.mark-as-paid {
  width: 400px;
}

.input {
  height: 40px;
  margin-top: 15px;
  border: 1px solid #e9e9e9;
  padding-left: 6px;
  caret-color: #9b82fe;
}

.input:-ms-input-placeholder {
  color: #adabab;
}

.input::placeholder {
  color: #adabab;
}

.input:focus {
  outline-color: #9b82fe;
  box-shadow: 0 0 6px #9b82fe;
}

.error {
  border-color: red;
}
